/* 두번쨰 페이지 */
.category {
  margin: 0 auto;
  width: 1060px;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.category-wrap {
  /* width: 1060px; */
  width: 100%;
  margin-top: 100px;
  display: flex;
  flex-direction: column;
}

.category-top {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}

.category-top__career {
  display: flex;
  font-size: 24px;
  align-items: center;
}

.category-top__career span {
  margin: 0px 15.5px 0px 0px;
}

.category-top__career-type {
  display: flex;
  font-weight: 700;
}

.category-top__career-detail {
  display: flex;
}

.bar {
  padding: 0px 24px 0px;
  opacity: 0.3;
  font-weight: 100;
}

.button {
  border: 1px solid black;
  padding: 3px;
  font-size: 15px;
  border-radius: 50%;
  opacity: 0.3;
  cursor: pointer;
}

.blue {
  color: blue;
}

.category-top__selections {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  margin: 30px 0px;
}

.category-top__selection-left {
  display: flex;
}

.category-top__selection-region {
  padding: 10px 15px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin-right: 10px;
  cursor: pointer;
  position: relative;
}

.category-top__selection-region:hover {
  background-color: #f9f9f9;
}

.category-top__selection-career {
  padding: 10px 15px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin-right: 10px;
  cursor: pointer;
}

.category-top__selection-career:hover {
  background-color: #f9f9f9;
}

.category-top__selection-tech {
  padding: 10px 15px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  cursor: pointer;
}

.category-top__selection-tech:hover {
  background-color: #f9f9f9;
}

.category-top__selection-right {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  cursor: pointer;
}

.category-top__selected-num {
  font-size: 12px;
  background-color: #36f;
  border-radius: 10px;
  color: #fff;
  font-weight: 600;
  margin-left: 7px;
  width: 20px;
  height: 20px;
  line-height: 20px;
  z-index: 1;
  display: inline-block;
  position: absolute;
  top: -6px;
  text-align: center;
}

select {
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
}

#sorting {
  border: none;
  font-size: 14px;
}

.category-buttons {
  width: 1060px;
}

.category-button {
  font-size: 13px;
  margin: 0px 8px 0px 0px;
  padding: 8px 14px;
  border: none;
  border-radius: 30px;
  height: 100%;
  cursor: pointer;
  background-color: #f3f3fb;
}

.category-button:hover {
  border: 1px solid #3366ff;
  padding: 7px 14px;
}

.job-list__main {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-top: 30px;
}

.top-section {
  display: flex;
  flex-direction: column;
  width: 66.25rem;
  margin-bottom: 10px;
  justify-content: center;
  margin-top: 30px;
}

.bookmark {
  color: #3366ff;
  font-size: 15px;
  font-weight: 600;
  line-height: 24px;
  cursor: pointer;
}

.top-section__title {
  font-size: 1.375rem;
  font-weight: 600;
  letter-spacing: -0.3px;
  color: #333;
  margin: 1.25rem 0 0.875rem;
}

.top-setion__companies {
  display: flex;
  width: 66.25rem;
  justify-content: space-between;
}

.top-section__company {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 16.9375rem;
  cursor: pointer;
  overflow: hidden;
  width: calc(100% / 5 - 10px);
}

.top-section__company:hover {
  color: #3366ff;
}

.top-section__company:hover .banner-image {
  transform: scale(1.1);
  transition: 0.5s;
}

.top-section__company-header {
  overflow: hidden;
}

.banner-image {
  height: 147px;
  border-radius: 3px 3px 0 0;
  background-position: 50%;
  background-size: cover;
}

.company-image {
  position: absolute;
  top: 125px;
  left: 16px;
  width: 50px;
  height: 50px;
  z-index: 0;
  background-position: 50%;
  box-shadow: inset 0 0 0 1px rgb(0 0 0 / 10%);
  border: 1px solid #e1e2e3;
  overflow: hidden;
}

.top-section__company-footer {
  display: flex;
  flex-direction: column;
  padding: 2.125rem 1rem 0rem;
  height: 5.625rem;
  border: 1px solid #e1e2e3;
  border-top: none;
  border-radius: 0 0 3px 3px;
  position: relative;
  /* width: 100%; */
}

.company-name {
  margin: 4px 0px;
  font-size: 16px;
}

.position-num {
  margin: 6px 0px 0px;
  font-size: 14px;
  color: #999999;
}

.recruitment .bookmark-icon {
  position: absolute;
  z-index: 0;
  left: 220px;
  top: 10px;
  font-weight: 400;
  color: #999;
  cursor: pointer;
}
